.quality {
	background: $gray-gradient2;
	padding: 10px 0 55px 0;
	.block-title {
		font-size: 30px;
		text-align: center;
		margin: 0 0 30px 0;
		@include mq($max-width: 1200px) {
			font-size: 28px; }
		@include mq($max-width: 800px) {
			font-size: 20px; } }
	.inner {
		position: relative; }
	.man {
		position: absolute;
		bottom: -55px;
		left: 50%;
		transform: translate(-50%, 0);
		display: block;
		z-index: 2;
		@include mq($max-width: 1200px) {
			display: none; } }
	.circles {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%); }
	.items {
		@include flex(row, between, null, wrap);
		@include mq($max-width: 1200px) {
			justify-content: center;
			align-items: flex-start; } }
	.item {
		@include flex(row, center, center, wrap);
		width: 50%;
		margin: 0 0 50px 0;
		@include mq($max-width: 1200px) {
			transform: translate(0,0) !important; }
		@include mq($max-width: 800px) {
			flex-direction: column !important;
			text-align: center !important; }
		@include mq($max-width: 575px) {
			width: 48%;
			margin: 3% 1%; }
		&:nth-child(even) {
			.item-inner {
				margin-left: 15px; } }
		&:nth-child(odd) {
			text-align: right;
			flex-direction: row-reverse;
			.item-inner {
				margin-right: 15px; } }
		&:nth-child(1) {
			transform: translate(-10%, 0); }
		&:nth-child(2) {
			transform: translate(10%, 0); }
		&:nth-child(3) {
			transform: translate(-20%, 0); }
		&:nth-child(4) {
			transform: translate(20%, 0); }
		&:nth-child(5) {
			transform: translate(-20%, 0); }
		&:nth-child(6) {
			transform: translate(20%, 0); }
		&:nth-child(7) {
			transform: translate(-10%, 0);
			margin: 0; }
		&:nth-child(8) {
			transform: translate(10%, 0);
			margin: 0; }
		&-icon {
			background: $yellow-gradient;
			width: 70px;
			height: 70px;
			border-radius: 100%;
			@include flex(row, center, center, wrap);
			@include mq($max-width: 800px) {
				margin: 0 0 15px 0; } }
		&-inner {
			max-width: 285px;
			width: 100%;
			@include mq($max-width: 800px) {
				margin: 0 !important; } }
		&__title {
			font-weight: 700;
			font-size: 20px;
			@include mq($max-width: 575px) {
				font-size: 14px; } }
		&__text {
			line-height: 1.4;
			@include mq($max-width: 575px) {
				font-size: 12px; } } } }
