.window-banner {
	background: $yellow-gradient;
	padding: 60px 0 30px 0;
	@include mq($max-width: 1200px) {
		padding: 30px 0; }
	.circles {
		position: absolute;
		left: 0;
		top: -60px;
		z-index: -1;
		transform: translate(-60%, 0);
		@include mq($max-width: 1000px) {
			display: none; } }
	.circles2 {
		position: absolute;
		right: 0;
		bottom: -30px;
		z-index: -1;
		transform: translate(15%, 0);
		@include mq($max-width: 1200px) {
			transform: translate(0, 0); }
		@include mq($max-width: 1000px) {
			display: none; } }
	.window {
		position: absolute;
		z-index: -1;
		left: 50%;
		bottom: -30px;
		transform: translate(-20%, 0);
		@include mq($max-width: 1000px) {
			display: none; } }
	.man {
		position: absolute;
		right: 0;
		bottom: -30px;
		z-index: -1;
		transform: translate(15%, 0);
		@include mq($max-width: 1200px) {
			display: none; } }
	.arrow {
		position: absolute;
		right: 0;
		bottom: 0;
		transform: translate(20%, 0);
		@include mq($max-width: 1000px) {
			display: none; } }
	.container {
		@include flex(row, null, center, wrap);
		position: relative;
		z-index: 3;
		@include mq($max-width: 800px) {
			flex-direction: column;
			align-items: flex-start; } }
	.block-title {
		margin: 0 0 20px 0;
		font-size: 30px;
		@include mq($max-width: 1000px) {
			font-size: 26px;
			line-height: 1; } }
	.block-text {
		color: white; }
	.inner {
		position: relative;
		max-width: 670px;
		width: 100%;
		@include mq($max-width: 1000px) {
			max-width: 100%; } }
	.form {
		max-width: 230px;
		width: 100%;
		margin: 0 auto 0 30px;
		@include mq($max-width: 1000px) {
			margin: 15px 0 0 0;
			max-width: 250px; }
		&-action {
			margin: 15px 0; }
		&__input {
			width: 100%;
			height: 50px; }
		&__btn {
			width: 100%;
			height: 50px; } } }
