.about {
	padding: 80px 0 20px 0;
	@include mq($max-width: 1200px) {
		padding: 30px 0; }
	.container {
		@include flex(row, null, end, wrap); }
	.block-title {
		text-align: center;
		width: 100%; }
	.line {
		padding: 10px 0;
		&.type1 {
			align-items: center; }
		&.type2 {
			background: $gray-gradient3;
			.text-container {
				padding: 0 0 0 30px;
				@include mq($max-width: 1000px) {
					padding: 0; } }
			img {
				@include mq($max-width: 1000px) {
					display: none; } } } }
	.text-container {
		padding: 30px 0;
		width: calc( 100% - 444px );
		@include mq($max-width: 1000px) {
			width: 100%;
			padding: 0; }
		ul {
			@include flex(row, between, start, wrap);
			@include mq($max-width: 575px) {
				display: block; }
			li {
				width: 45%;
				padding: 5px 10px 5px 0;
				@include mq($max-width: 575px) {
					width: 100%; } } } }
	.img {
		display: block;
		max-width: 444px;
		width: 100%;
		height: auto; } }
