.mainblock {
	padding: 78px 0 102px 0;
	@include mq($max-width: 1200px) {
		padding: 50px 0; }
	&.index {
		@include bg(null, null, 'mainblock/index/bg.jpg', no-repeat, center, center, cover);
		@include mq($max-width: 575px) {
			@include bg(null, null, 'mainblock/index/bg2.jpg', no-repeat, center, bottom, contain); } }
	&.glazing-balconies {
		@include bg(null, null, 'mainblock/glazing-balconies/bg.jpg', no-repeat, center, center, cover); }
	&.installation-plastic-doors {
		@include bg(null, null, 'mainblock/installation-plastic-doors/bg.jpg', no-repeat, center, center, cover); }
	&.repairs-balconies {
		@include bg(null, null, 'mainblock/repairs-balconies/bg.jpg', no-repeat, center, center, cover); }
	&.finishing-balconies {
		@include bg(null, null, 'mainblock/finishing-balconies/bg.jpg', no-repeat, center, center, cover); }
	&.glazing-windows {
		@include bg(null, null, 'mainblock/glazing-windows/bg.jpg', no-repeat, center, center, cover); }
	.container {
		@include flex(row, between, start, wrap); }
	.left {
		max-width: 681px;
		width: 100%;
		@include mq($max-width: 1200px) {
			max-width: calc( 100% - 350px ); }
		@include mq($max-width: 700px) {
			max-width: 100%;
			margin: 0 0 15px 0; } }
	.right {
		max-width: 346px;
		width: 100%;
		padding: 28px 41px;
		box-shadow: 0px 4px 20px rgba(51, 51, 51, 0.1);
		border-radius: 10px;
		@include bg(null, white, 'mainblock/percent.png', no-repeat, right, top, auto);
		@include mq($max-width: 575px) {
			padding: 15px; } }
	.form-bigtitle {
		text-transform: uppercase;
		font: 800 26px/1 'Exo', sans-serif;
		padding: 15px; }
	.form {
		&-social {
			border-radius: 4px;
			overflow: hidden;
			box-shadow: 0px 4px 20px rgba(51, 51, 51, 0.1); }
		&__input {
			width: 100%;
			background: $gray2; }
		&__btn {
			margin: 15px 0;
			width: 100%; }
		&__text {
			text-align: center; } }

	&__title {
		font: 900 48px/1.2 'Exo', sans-serif;
		text-transform: uppercase;
		@include mq($max-width: 1200px) {
			font-size: 38px; }
		@include mq($max-width: 575px) {
			font-size: 30px; }
		.yellow {
			color: $yellow; }
		.mini {
			font-size: 30px;
			@include mq($max-width: 1200px) {
				font-size: 24px; }
			@include mq($max-width: 575px) {
				font-size: 22px; } } }
	&-protect {
		margin: 29px 0 34px 0;
		@include flex(row, null, center, wrap);
		@include mq($max-width: 575px) {
			margin: 15px 0; }
		&__icon {
			display: block;
			width: 46px;
			margin: 0 16px 0 0;
			@include mq($max-width: 575px) {
				display: none; } }
		&__text {
			line-height: 1.5;
			max-width: 400px; } }
	&-delivery {
		background: $dark;
		border-radius: 4px;
		padding: 10px 30px;
		color: white;
		@include flex(row, between, center, wrap);
		display: inline-flex;
		@include mq($max-width: 575px) {
			padding: 10px; }
		&-inner {
			margin: 0 10px 0 0; }
		&__title {
			font: 900 24px/1 'Exo', sans-serif; }
		&__text {
			margin: 10px 0 0 0;
			font: 400 14px/1 'Exo', sans-serif; }
		&__icon {
			display: block;
			width: 66px;
			@include mq($max-width: 575px) {
				display: none; } } }
	.list {
		margin: 27px 0 0 0;
		@include flex(row, start, start, wrap);
		&-item {
			margin: 0 50px 0 0;
			@include mq($max-width: 1200px) {
				margin: 0 2%;
				max-width: 30%; }
			@include mq($max-width: 800px) {
				max-width: 46%; }
			@include mq($max-width: 575px) {
				width: 48%; }
			&__title {
				font: 800 18px 'Exo', sans-serif;
				@include mq($max-width: 1200px) {
					font-size: 16px; }
				@include mq($max-width: 575px) {
					font-size: 15px; }
				span {
					font-size: 72px;
					@include mq($max-width: 1200px) {
						font-size: 50px; }
					@include mq($max-width: 575px) {
						font-size: 36px; } } }
			&__text {
				margin: 10px 0 0 0;
				font-weight: 500;
				@include mq($max-width: 575px) {
					font-size: 14px; } } } } }
