.mini-banner {
	background: $yellow-gradient;
	padding: 55px 0;
	@include mq($max-width: 1200px) {
		padding: 30px 0; }
	.container {
		position: relative; }
	.circles {
		position: absolute;
		z-index: 1;
		right: 0;
		bottom: -55px;
		transform: translate(60%, 0);
		@include mq($max-width: 1200px) {
			transform: translate(0, 0); } }
	.percent {
		position: absolute;
		top: 50%;
		left: 0;
		transform: translate(-20%, -50%);
		@include mq($max-width: 1200px) {
			display: none; } }
	.inner {
		position: relative;
		z-index: 2;
		max-width: 790px;
		width: 100%;
		margin: 0 0 0 auto;
		@include mq($max-width: 1200px) {
			margin: 0 auto 0 0; } }
	&__title {
		text-transform: uppercase;
		font: 800 50px 'Exo', sans-serif;
		@include mq($max-width: 800px) {
			font-size: 38px; }
		@include mq($max-width: 700px) {
			font-size: 32px; }
		@include mq($max-width: 575px) {
			font-size: 24px; }
		span {
			font-size: 36px;
			@include mq($max-width: 700px) {
				font-size: 30px; }
			@include mq($max-width: 575px) {
				font-size: 24px; } } }
	.btn-white {
		position: absolute;
		right: 0;
		bottom: 0;
		transform: translate(0, 20%);
		@include mq($max-width: 800px) {
			position: static; } } }


