.q-banner {
	background: $yellow-gradient;
	padding: 80px 0 90px 0;
	position: relative;
	@include mq($max-width: 1200px) {
		padding: 30px 0; }
	&.type2 {
		.block-title {
			line-height: 1; } }
	.container {
		position: relative;
		max-width: 1140px; }
	.window {
		position: absolute;
		left: -90px;
		bottom: -90px;
		@include mq($max-width: 1200px) {
			display: none; } }
	.girl {
		z-index: 2;
		position: absolute;
		left: 0;
		bottom: -90px;
		@include mq($max-width: 1200px) {
			display: none; } }
	.circle {
		position: absolute;
		right: 0;
		bottom: 0; }
	.inner {
		max-width: 670px;
		width: 100%;
		margin: 0 0 0 auto;
		position: relative;
		z-index: 2;
		@include mq($max-width: 1200px) {
			margin: 0 auto 0 0; } }
	.form {
		margin: 46px 0 0 0;
		@include mq($max-width: 700px) {
			margin: 15px 0 0 0;
			max-width: 250px;
			width: 100%; }
		&__input {
			margin: 0 25px 0 0;
			@include mq($max-width: 700px) {
				width: 100%;
				margin: 0 0 15px 0; } }
		&__btn {
			width: 280px;
			@include mq($max-width: 700px) {
				width: 100%; } } } }
